.card-modal {
  position: relative;
  bottom: 55px;
  background: #8bc34a;
  align-items: center;
  display: none;
  flex-flow: column wrap;
  padding: 1.5rem 1.25rem 1.25rem;
  margin: 0 -1.25rem;
}

.card-modal--yellow {
  background: #fdd835 !important;
}

.card-modal--blue {
  background: #46bcec !important;
}

.card-modal--white {
  background: #fff !important;
}

.card-modal--rel {
  margin: 0;
  bottom: 0;
}

.card-modal--pos-right {
  margin: 0 -1.25rem;
}

.card-modal__inner {
  display: flex;
  align-items: center;
  justify-content: space-around;
  flex-flow: column wrap;
  min-width: 280px;
}

.card-modal__title {
  font-weight: 900;
  font-size: 1rem;
  line-height: 1.4rem;
  padding-bottom: 17px;
  width: 100%;
  text-align: center;
}

.card-modal__date {
  display: flex;
  align-items: center;
  justify-content: center;
  max-width: 335px;
  width: 100%;
  margin: 5px 0;
}

.card-modal__date-right,
.card-modal__date-left {
  flex: 1;
  border: 2px solid #fff;
  padding: 14px 10px;
  font-weight: 400;
  font-size: 0.9rem;
  line-height: 1rem;
  background: #8bc34a;
}

.card-modal__date-right {
  border-radius: 0 15px 15px 0;
  border-left: 0;
}

.card-modal__date-left {
  border-radius: 15px 0 0 15px;
}

.card-modal__item {
  font-weight: 400;
  font-size: 0.9rem;
  line-height: 1rem;
  border-radius: 15px;
  border: 2px solid #fff;
  background: #8bc34a;
  margin: 5px 0;
  padding: 0.9rem 0;
  width: 100%;
  max-width: 335px;
}

.card-modal__item--yellow {
  background: #fdd835 !important;
}

.card-modal__item--white {
  background: #fff !important;
  border: 2px solid #f2f2f2 !important;
}

.card-modal__item--dark {
  background: #000 !important;
  color: #fff !important;
}

.card-modal__item--blue {
  background: #46bcec !important;
}

.card-modal__button {
  font-weight: 900;
  font-size: 1.1rem;
  line-height: 1.3rem;
  background: #fff;
  color: #000;
  order: 1000;
}

.card-modal__button--mtop {
  margin: 2.8rem 0;
}

.card-modal__close {
  position: relative;
  width: 15px;
  height: 24px;
  margin-top: 15px;
}

.card-modal__close span {
  position: absolute;
  height: 4px;
  width: 24px;
  background: #000;
  transform: rotate(45deg);
  right: 0;
}

.card-modal__close::before {
  position: absolute;
  content: "";
  height: 4px;
  width: 24px;
  background: #000;
  transform: rotate(-45deg);
  right: 0;
}

.modal-upload {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-flow: row wrap;
  order: 999;
  width: 100%;
}

.modal-upload__text {
  margin-left: 10px;
  font-weight: 900;
  font-size: 1rem;
  line-height: 1.2rem;
}

.telephone {
  padding-bottom: 5rem;
}

.telephone-inner {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-flow: row wrap;
}

.icon-phone {
  margin: 2.81rem auto 1.25rem;
  order: 0;
}

.telephone-block {
  flex-basis: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-flow: row wrap;
  font-weight: 400;
  font-size: 2rem;
  line-height: 2.3rem;
  margin-bottom: 10px;
}

.telephone-block__text {
  font-weight: 400;
  font-size: 0.9rem;
  line-height: 1.3rem;
  flex: 100%;
  text-align: center;
}

.icon-phone {
  display: none;
}

.breadcrumbs {
  display: flex;
  align-items: center;
}

.breadcrumbs__text {
  margin-left: 10px;
}

.head-title {
  font-weight: 900;
  font-size: 1.75rem;
  line-height: 1.95rem;
}

.slider {
  position: relative;
}

.car-detail {
  padding-top: 4.6rem;
  font-weight: 400;
  font-size: 0.9rem;
  line-height: 1.3rem;
}

.car-detail__head {
  margin-top: 0.9rem;
}

.car-detail__item {
  padding: 1rem 0;
  line-height: 16px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-top: 1px solid #d5d5d5;
}

.car-detail__item:last-child {
  border-bottom: 1px solid #d5d5d5;
}

.car-detail__item-name {
  color: #939393;
}

.car-detail__footer {
  padding-bottom: 40px;
}

.telephone-inner {
  padding-bottom: 2.5rem;
}

.telephone-block {
  margin-bottom: 5px;
}

.telephone-block__number {
  font-size: 24px;
}

.swiper-container {
  width: 100%;
  height: 300px;
  margin-left: auto;
  margin-right: auto;
}

.swiper-slide {
  background-size: cover;
  background-repeat: no-repeat;
}

.swiper-button-next,
.swiper-button-prev {
  top: 84%;
  width: 8px;
  outline: none;
}

.swiper-button-next::after,
.swiper-button-prev::after {
  font-size: 16px;
  font-weight: 700;
}

.swiper-button-next {
  right: 0;
}

.swiper-button-prev {
  left: 0;
}

.gallery-top {
  width: 347px;
  height: 185px;
}

.gallery-thumbs {
  height: 83px;
  box-sizing: border-box;
  padding: 10px 0;
  margin-bottom: 40px;
  width: 80%;
}

.gallery-thumbs .swiper-slide {
  height: 100%;
  border-radius: 15px;
}

@media (min-width: 768px) {
  .card-modal {
    position: fixed;
    margin: 0;
    z-index: 1;
    border-radius: 15px;
    box-shadow: 0px 20px 50px rgba(0, 0, 0, 0.15);
  }

  .card-modal__inner {
    min-width: 335px;
  }

  .card-modal--rel {
    position: relative;
    border-radius: 0;
  }

  .card-modal__title {
    flex: 100%;
    text-align: center;
  }

  .modal-upload__text {
    margin-right: auto;
  }

  .icon-images {
    margin-left: auto;
  }

  .icon-phone {
    order: 2;
    margin: auto 0;
  }

  .telephone {
    padding: 30px 0;
  }

  .telephone-block {
    flex: 1;
    width: 205px;
  }

  .icon-phone {
    display: block;
    margin: 0 1.5rem;
  }

  .gallery-top {
    height: 303px;
    width: 568px;
  }

  .gallery-thumbs {
    width: 95%;
  }

  .swiper-button-next,
  .swiper-button-prev {
    top: 90%;
  }
}

@media (min-width: 768px) and (orientation: landscape) {
  .card-modal--transform {
    transform: scale(0.8);
    bottom: -20px;
  }
}

@media (min-width: 1200px) {
  .card-modal--pos-right {
    margin: 0 -80px;
  }
}

@media (min-width: 1200px) and (orientation: landscape) {
  .card-modal--transform {
    transform: scale(1);
    bottom: 55px;
  }
}

@media (min-width: 1440px) {
  .card-modal--pos-right {
    box-shadow: none;
  }

  .card-modal--rel {
    flex-flow: row wrap;
    justify-content: center;
    padding: 3.1rem 1.7rem 4rem;
    margin: 0;
  }

  .card-modal__inner {
    max-width: 1280px;
    width: 100%;
  }

  .card-modal__inner--flex-row {
    flex-flow: row wrap;
  }

  .card-modal__title {
    font-size: 1.5rem;
    line-height: 29px;
  }

  .card-modal__item {
    flex: 1;
  }

  .card-modal__item--mr40:not(:last-child) {
    margin-right: 40px;
  }

  .card-modal__button--mtop {
    margin: 2.8rem 0 0;
  }

  .modal-upload {
    order: 0;
  }

  .telephone-block {
    flex: 0 1 auto;
    width: auto;
  }

  .car-detail__card {
    max-width: 620px;
    margin-right: 40px;
  }

  .container {
    display: flex;
    align-items: flex-start;
  }

  .gallery-thumbs {
    width: 85%;
  }
}